import { NgModule } from '@angular/core';
import { MatExpansionModule } from '@angular/material/expansion'; 
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

// routing
import { RouterModule } from '@angular/router';
import { AppRoutingModule } from './app-routing.module';

//common
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { MobileMenuComponent } from './mobile-menu/mobile-menu.component';

// pages
import { AppComponent } from './app.component';
import { AboutpageComponent } from './aboutpage/aboutpage.component';
import { FaqpageComponent } from './faqpage/faqpage.component';
import { HomepageComponent } from './homepage/homepage.component';
import { BorrowpageComponent } from './borrowpage/borrowpage.component';
import { EarnpageComponent } from './earnpage/earnpage.component';
import { ErrorpageComponent } from './errorpage/errorpage.component';
import { GraphQLModule } from './graphql.module';
import { HttpClientModule } from '@angular/common/http';



@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    HomepageComponent,
    FooterComponent,
    AboutpageComponent,
    FaqpageComponent,
    MobileMenuComponent,
    BorrowpageComponent,
    EarnpageComponent,
    ErrorpageComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    MatExpansionModule,
    GraphQLModule,
    HttpClientModule,
    RouterModule.forRoot([
      {path: 'home', component: HomepageComponent, data: {animation: 'HomePage'} },
      {path: 'about', component: AboutpageComponent, data: {animation: 'AboutPage'}},
      {path: 'faq', component: FaqpageComponent, data: {animation: 'FaqPage'}},
      {path: 'earn', component: EarnpageComponent, data: {animation: 'EarnPage'}},
      {path: 'borrow', component: BorrowpageComponent, data: {animation: 'BorrowPage'}},
      {path: '', component: HomepageComponent, redirectTo: '', pathMatch: 'full'},
      {path: '**', component: ErrorpageComponent }
    ])
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
